import {createRouter, createWebHistory} from "vue-router";
import Home from "@/view/Home";
import Language from "@/view/Language";
import Record from "@/view/Record";
import RecordItem from "@/view/RecordItem";
import History from "@/view/History";
import About from "@/view/About";
import NewComment from "@/view/NewComment";
import Profile from "@/view/Profile";
import SelectWorker from "@/view/SelectWorker";
import AboutWorker from "@/view/AboutWorker";
import SelectDate from "@/view/SelectDate";
import SelectService from "@/view/SelectService";
import Ordering from "@/view/Ordering";
import Purse from "@/view/Purse";
import Certificate from "@/view/Certificate";
import Comments from "@/view/Comments";

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/language',
        name: '',
        component: Language
    },
    {
        path: '/record',
        name: ' ',
        component: Record,
    },
    {
        path: '/record/:id',
        name: '',
        component: RecordItem,
        props: true
    },
    {
        path: '/history',
        name: '',
        component: History
    },
    {
        path: '/about',
        name: ' ',
        component: About
    },
    {
        path: '/new-comment',
        name: ' ',
        component: NewComment
    },
    {
        path: '/profile',
        name: ' ',
        component: Profile
    },
    {
        path: '/select-worker',
        name: ' ',
        component: SelectWorker
    },
    {
        path: '/worker/:id',
        name: ' ',
        component: AboutWorker,
        props: true
    },
    {
        path: '/select-date',
        name: '',
        component: SelectDate
    },
    {
        path: '/select-service',
        name: ' ',
        component: SelectService
    },
    {
        path: '/ordering',
        name: ' ',
        component: Ordering
    },
    {
        path: '/purse',
        name: '',
        component: Purse
    },
    {
        path: '/store',
        name: '',
        component: Certificate
    },
    {
        path: '/comments',
        name: '',
        component: Comments
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router